/* フェード設定 */
/* ============================================ */
.c-hamburger {
  display: none;
}
@media screen and (max-width: 768px) {
  .c-hamburger {
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    background-color: transparent;
    cursor: pointer;
    width: 25px;
    height: 25px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
}

.c-hamburger__line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 18px;
  height: 2px;
  background-color: #333;
  -webkit-transition: inherit;
  transition: inherit;
}

.c-hamburger__line::before,
.c-hamburger__line::after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: inherit;
  content: "";
  -webkit-transition: inherit;
  transition: inherit;
}

.c-hamburger__line::before {
  top: -5px;
}

.c-hamburger__line::after {
  top: 5px;
}

.c-hamburger[aria-expanded=true] .c-hamburger__line {
  background-color: transparent;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before, .c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  top: 0;
  background-color: #fff;
}

.c-hamburger[aria-expanded=true] .c-hamburger__line::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.c-page-top {
  width: 2.8125rem;
  height: 2.8125rem;
  background: #fff;
  position: fixed;
  bottom: 3.125rem;
  right: 1.25rem;
  transition: 0.3s;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 100;
}

.c-page-top.is-active {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.c-page-top span:first-child {
  display: inline-block;
  width: 1.25rem;
  height: 0.125rem;
  background: #333;
  transform: rotate(-45deg) translateX(3px);
  transform-origin: right;
  transition: 0.3s;
}

.c-page-top span:nth-child(2) {
  display: inline-block;
  width: 1.25rem;
  height: 0.125rem;
  background: #333;
  transform: rotate(45deg) translateX(-3px);
  transform-origin: left;
  transition: 0.3s;
}

.c-page-top:hover {
  background: #333;
}
.c-page-top:hover span {
  background: #fff;
}

.l-inner {
  width: 100%;
  max-width: 1250px;
  padding-right: 25px;
  padding-left: 25px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 768px) {
  .l-inner {
    max-width: 37.5rem;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.l-parts {
  width: 100%;
  height: 100%;
  background-color: #9cdbe8;
  padding: 6.25rem 0;
}

.l-parts__sec {
  padding: 3.125rem 0;
}

.p-footer {
  padding: 2rem 2rem 3.75rem;
}

.p-footer__inner {
  display: flex;
  align-items: center;
}

.p-footer__logo {
  width: 4.375rem;
}

.p-footer__body {
  margin-left: auto;
}

.p-footer__items {
  display: flex;
  align-items: center;
}

.p-footer__item {
  font-size: 1rem;
  position: relative;
  color: #fff;
  text-align: center;
}

.p-footer__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: 0.3125rem;
  background: yellow;
  opacity: 0;
  transition: 0.3s;
}

.p-footer__item:hover::after {
  opacity: 1;
}

.p-footer__item + .p-footer__item {
  margin-top: initial;
  margin-left: 2rem;
}

.p-footer__copy {
  font-size: 0.6875rem;
  color: #fff;
  text-align: center;
  padding-bottom: 3.75rem;
}

@media screen and (max-width: 768px) {
  .p-footer {
    padding: 3.625rem 0 2rem;
  }
  .p-footer__inner {
    display: block;
  }
  .p-footer__logo {
    width: 4.375rem;
    margin: auto;
  }
  .p-footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .p-footer__body {
    margin-top: 0.5rem;
  }
  .p-footer__items {
    display: block;
  }
  .p-footer__item + .p-footer__item {
    margin-top: 0.5rem;
    margin-left: initial;
  }
  .p-footer__copy {
    font-size: 0.6875rem;
  }
}
.p-header-menu {
  height: inherit;
}

.p-header-menu__items {
  display: flex;
  height: inherit;
  align-items: center;
  padding: 0;
}

.p-header-menu__item {
  height: inherit;
  color: #fff;
}

.p-header-menu__item + .p-header-menu__item {
  margin-left: 1.875rem;
}

.p-header-menu__item a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .p-header-menu {
    position: fixed;
    width: 100%;
    max-width: 32.5rem;
    height: 100vh;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-y: scroll;
    transform: translateX(2000px);
    transition: 0.4s;
    z-index: 4;
  }
  .is-drawerActive .p-header-menu {
    background-color: #000;
    transform: translateX(0px);
  }
  .p-header-menu__items {
    height: initial;
    display: block;
    padding: 7.5rem 0 6.25rem;
  }
  .p-header-menu__item + .p-header-menu__item {
    margin-left: initial;
  }
  .p-header-menu__item a {
    display: inline-block;
    width: 100%;
    height: 100%;
    padding: 0.625rem 0;
    text-align: center;
  }
}
.p-header {
  height: 3.125rem;
  background: rgba(17, 17, 17, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  transition: 0.3s;
}

.p-header.change-color {
  background: #111111;
}

.p-header__inner {
  display: flex;
  padding: 0 1.5625rem;
  height: inherit;
}
@media screen and (max-width: 768px) {
  .p-header__inner {
    padding: 0 1.25rem;
  }
}

.p-header__logo {
  width: 5.9625rem;
  height: inherit;
  display: flex;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .p-header__logo {
    width: 4.375rem;
  }
}

.p-header__logo img {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.p-header__drawer {
  position: absolute;
  top: 1.5625rem;
  transform: translateY(-50%);
  right: 1.25rem;
  z-index: 102;
}

.p-header__overlay {
  position: fixed;
  z-index: 101;
  width: 100vw;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  background-color: #000;
  color: #fff;
  visibility: hidden;
  opacity: 0;
}

.is-drawerActive .p-header__overlay {
  visibility: visible;
  opacity: 0.8;
  transition: 0.4s;
}

.p-header__menu {
  margin-left: auto;
}
@media screen and (max-width: 768px) {
  .p-header__menu {
    margin-left: initial;
  }
}

.u-hidden-pc {
  display: none;
}
@media screen and (max-width: 768px) {
  .u-hidden-pc {
    display: block;
  }
}

.u-hidden-sp {
  display: block;
}
@media screen and (max-width: 768px) {
  .u-hidden-sp {
    display: none;
  }
}